var $loader;

module.exports = {
    /**
     * @function
     * @description Shows an AJAX-loader on top of a given container
     * @param {Element} container The Element on top of which the AJAX-Loader will be shown
     */
    show: function(container) {
        var $loaderTarget = (!container || $(container).length === 0) ? $('body') : $(container),
            loaderHTML = '';
    
        $loader = $loader || $('.b-loader');
        if ($loader.length === 0) {
            loaderHTML += '<div class="b-loader__indicator">';
            loaderHTML += '<span class="b-loader__indicator-item"></span>';
            loaderHTML += '<span class="b-loader__indicator-item"></span>';
            loaderHTML += '<span class="b-loader__indicator-item"></span>';
            loaderHTML += '<span class="b-loader__indicator-item"></span>';
            loaderHTML += '<span class="b-loader__indicator-item"></span>';
            loaderHTML += '</div>';
            $loader = $('<div/>').addClass('b-loader').html(loaderHTML);
        }

        return $loader.appendTo($loaderTarget).show();
    },

    /**
     * @function
     * @description Hides an AJAX-loader
     */
    hide: function(container) {
        if (container) {
            $loader = $(container).find('.b-loader');
        }
        if ($loader) {
            $loader.hide();
        }
    }
}
