'use strict'

var util = require('./../util');

var initialized = false,
    tooltipDefaultOptions = require('./../components-config/tooltip-config-default'),
    tooltipInitOptions = {
        'open': function(event, ui) {
            var $this = $(this);

            ui.tooltip.one('click', '.js-tooltip__close-button', function() {
                $this.customTooltip('close');
            });

            if ($this.customTooltip('option', 'closeOnBlur')) {
                $this.on('blur', function() {
                    $this.customTooltip('close');
                });
            }

            util.throttle(function() {
                $(document).one('window.resize search.active', function() {
                    $this.customTooltip('close');
                })
                // close tooltip on outside click
                .on('click.tooltipclose', function(e) {
                    if ($(e.target).closest('.ui-tooltip').length > 0) return;
                    $this.customTooltip('close');
                    $(this).off('click.tooltipclose');
                });
            });
        },
        'close': function() {
            $(document).off('click.tooltipclose');
        },
        'position': {
            'at': 'center top-10',
            'my': 'center bottom-10',
            'using': function(position, feedback) {
                var arrowHolderWidth = feedback.target.width;

                if (feedback.element.left === 0) {
                    arrowHolderWidth += feedback.target.left * 2;
                } else {
                    var windowWidth = $(window).width();

                    if (windowWidth - feedback.element.left - feedback.element.width === 0) {
                        feedback.horizontal = 'right';
                        arrowHolderWidth += (windowWidth - feedback.target.left - feedback.target.width) * 2;
                    }
                }

                $(this).css(position)
                    .find('.js-tooltip__arrow-holder')
                        .addClass(feedback.vertical)
                        .addClass(feedback.horizontal)
                        .css('min-width', arrowHolderWidth);
            }
        }
    };

/**
 * @function
 * @description Initialize default tooltip options
 * @param {Object} params
 * @returns {void}
 */
function initializeConfig(params) {
    tooltipInitOptions = $.extend({}, tooltipInitOptions, tooltipDefaultOptions, params || {});
}

/**
 * @function
 * @description Generate tooltip content template
 * @param {String} content of tooltip
 * @param {Boolean} options
 * @returns {String}
 */
function getTooltipContent(content, showCloseButton) {
    var tooltipContent = '';

    tooltipContent += '<div class="b-tooltip__content">';
    tooltipContent += '<div class="b-tooltip__body">';
    tooltipContent += content;
    tooltipContent += '</div>';

    if (showCloseButton) {
        tooltipContent += '<div class="b-tooltip__close">';
        tooltipContent += '<button class="b-tooltip__close-button js-tooltip__close-button i-font-close"></button>';
        tooltipContent += '</div>';
    }

    tooltipContent += '</div>';
    tooltipContent += '<div class="b-tooltip__arrow-holder js-tooltip__arrow-holder"><div class="b-tooltip__arrow"></div></div>';

    return tooltipContent;
}

/**
 * @function
 * @description Collect all tooltip options
 * @param {Object} $element for which the tooltip will be created
 * @param {Object} options
 * @returns {Object}
 */
function getTooltipOptions($tooltip, options) {
    var tooltipDataOptions = $tooltip.data('tooltip') || {},
        tooltipOptions = $.extend({}, tooltipInitOptions, tooltipDataOptions, options || {});

    tooltipOptions.position = $.extend({}, tooltipInitOptions.position, tooltipDataOptions.position, {});
    tooltipOptions.items = $tooltip;

    tooltipOptions.content = getTooltipContent(!tooltipOptions.content ? $tooltip.find('.js-tooltip__content').html() : tooltipOptions.content, (tooltipOptions.openOnClick || tooltipOptions.openExternal) && tooltipOptions.showCloseButton);

    return tooltipOptions;
}

/**
 * @function
 * @description Initialize custom tooltip plugin based on jQuery UI Tooltip
 * @returns {void}
 */
function initializePlugin() {
    $.widget('custom.customTooltip', $.ui.tooltip, {
        disableEventBinding: false,
        _create: function() {
            if (this.options.openOnClick) {
                this._on({
                    click: 'open',
                    touchstart: 'open'
                });
            }

            if (this.options.openOnClick || this.options.openExternal) {
                this.disableEventBinding = true;
            }

            this._super();
        },

        _tooltip: function(element) {
            var tooltip = $('<div>').attr('role', 'tooltip').addClass('ui-tooltip ui-widget ui-corner-all ui-widget-content ' + (this.options.tooltipClass || '')),
                id = tooltip.uniqueId().attr('id');

            $('<div>')
                .addClass('ui-tooltip-content')
                .appendTo(tooltip);

            if (!this.options.appendTo) {
                // append tooltip inside a dialog if element is a part of the last one
                this.options.appendTo = $(element).closest('.ui-dialog-content, .ui-dialog-custom-content');
                if (this.options.appendTo.length === 0) {
                    this.options.appendTo = this.document[0].body
                }
            }

            tooltip.appendTo(this.options.appendTo);

            this.tooltips[id] = {
                element: element,
                tooltip: tooltip
            };

            return this.tooltips[id];
        },

        _on: function(suppressDisabledCheck, element, handlers) {
            if (this.disableEventBinding) return;

            $(element).filter('[href]').on('click touchstart', function(event) {
                event.preventDefault();
            });

            this._super(suppressDisabledCheck, element, handlers);
        }
    });
}

/**
 * @function
 * @description Initialize all tooltips
 * @param {Object} params
 * @returns {void}
 */
function initializeTooltip(params) {
    $(params.container || document).find('.js-tooltip').each(function() {
        tooltip.create($(this), params);
    });
}

var tooltip = {
    /**
     * @function
     * @description Initialize component
     * @param {Object} params - tooltip options
     */
    init: function(params) {
        if (!initialized) {
            initializePlugin();
            initialized = true;
        }

        initializeConfig(params);
        initializeTooltip(params);
    },

    /**
     * @function
     * @description Create a tooltip with options
     * @param {Object} $element for which the tooltip will be created
     * @param {Object} options
     * @param {String} options.content contains the html of the tooltip content
     */
    create: function($element, options) {
        if (!initialized) {
            initializePlugin();
            initialized = true;
        }

        $element.customTooltip(getTooltipOptions($element, options));
        $element.data('tooltipCreated', true);
    },

    /**
     * @function
     * @description Opens a tooltip with options
     * @param {Object} $element for which the tooltip will be opened
     * @param {Object} options
     * @param {String} options.content contains the html of the tooltip content
     */
    open: function($element, options) {
        if (!$element.data('tooltipCreated')) {
            this.create($element, options);
        }
        $element.customTooltip('open');
    },

    /**
     * @function
     * @description Closes the tooltip
     * @param {Object} $element for which the tooltip will be closed
     */
    close: function($element) {
        if ($element.data('tooltipCreated')) {
            $element.customTooltip('close');
        }
    }
};

module.exports = tooltip;
