'use strict'

require('slick-carousel');

var layout = require('./../utils/layout'),
    carouselDefaultOptions = require('./../components-config/carousel-config-default'),
    carouselInitOptions = {},
    initialized = false;

/**
 * Merge carousel params with default
 * @param {Object} params - slick carousel options
 */
function initCarouselOptions(params) {
    carouselInitOptions = $.extend({}, carouselDefaultOptions, params || {});
}

/**
 * Extend carousel plugin
 * @param {Object} slickObject - an instance of slick carousel
 */
function initCarouselPlugin(slickObject) {
    // Reason to override: carousel don't scroll to slide if this slide is placed on last page and this page have less slide number than option slideToShow
    slickObject.getNavigableIndexes = function() {
        var _ = this,
            breakPoint = 0,
            counter = 0,
            indexes = [],
            max;

        if (_.options.infinite === false) {
            max = _.slideCount;
            if (!_.options.centerMode) max = _.slideCount - _.options.slidesToShow + 1; // added line
        } else {
            breakPoint = _.options.slidesToScroll * -1;
            counter = _.options.slidesToScroll * -1;
            max = _.slideCount * 2;
        }

        while (breakPoint < max) {
            indexes.push(breakPoint);
            breakPoint = counter + _.options.slidesToScroll;
            counter += _.options.slidesToScroll <= _.options.slidesToShow ? _.options.slidesToScroll  : _.options.slidesToShow;
        }

        if (indexes.indexOf(_.slideCount - parseInt(_.options.slidesToShow, 10)) === -1) indexes.push(_.slideCount - parseInt(_.options.slidesToShow, 10)); // added line

        return indexes;
    };

    slickObject.getSlideCount = function() {
        var _ = this,
            slidesTraversed, swipedSlide, centerOffset, slideTrackWidth;

        centerOffset = _.options.centerMode === true ? _.slideWidth * Math.floor(_.options.slidesToShow / 2) : 0;

        if (_.options.swipeToSlide === true) {
            slideTrackWidth = _.$slideTrack.outerWidth();
            _.$slideTrack.find('.slick-slide').each(function(index, slide) {
                var slideOffset = _.options.rtl ? slideTrackWidth - slide.offsetLeft : slide.offsetLeft; // fix for correct swiping of infinite carousel on RTL-locale
                if (slideOffset - centerOffset + ($(slide).outerWidth() / 2) > (_.swipeLeft * -1)) {
                    swipedSlide = slide;
                    return false;
                }
            });

            slidesTraversed = Math.abs($(swipedSlide).attr('data-slick-index') - _.currentSlide) || 1;

            return slidesTraversed;
        } else {
            return _.options.slidesToScroll;
        }
    };
}

/**
 * Initialize slick carousel component on selector
 * @param {Object} selector
 * @param {Object} params
 */
function initSlick(selector, params) {
    var $carousel = $(selector),
        carouselDataOptions = $carousel.data('carousel') || {},
        carouselOptions = $.extend({}, carouselInitOptions, carouselDataOptions, params || {});

    if ('initOnBreakpoint' in carouselDataOptions) {
        carouselOptions.initOnBreakpoint = carouselDataOptions.initOnBreakpoint;
    }

    var initOnBreakpoint = carouselOptions.initOnBreakpoint.length === 0 || carouselOptions.initOnBreakpoint.indexOf(layout.getMode()) !== -1;
    if (initOnBreakpoint && carouselOptions.autoload) {
        if (carouselOptions.asNavForCarousel) {
            var $mainCarousel = $(carouselOptions.asNavForCarousel);

            $mainCarousel.on('beforeChange', function(event, slick, currentSlide, nextSlide) {
                var $slickSlides = $carousel.find('.slick-slide')
                $slickSlides.removeClass('slick-selected').filter('[data-slick-index=' + nextSlide + ']').addClass('slick-selected');
                $carousel.slick('slickGoTo', nextSlide);
            })

            $carousel.on('click', '.slick-slide', function() {
                var $this = $(this);
                $this.addClass('slick-selected');
                $mainCarousel.slick('slickGoTo', $this.data('slickIndex'));
            });
        }

        $carousel.on('init', function(event, slick) {
            initCarouselPlugin(slick);
        }).on('init reInit breakpoint', function(event, slick) {
            var slidesToShow = slick.options.slidesToShow;

            //Add specific class if carousel have float number slides to show
            $carousel.toggleClass('b-carousel--shifted', slick.slideCount && slick.options.infinite && slidesToShow !== parseInt(slidesToShow, 10) && slick.slideCount > slidesToShow);

            // Add specific class if carousel don't have scrolling
            $carousel.toggleClass('b-carousel--no-scroll', slick.slideCount && slick.slideCount <= slidesToShow);

            // Vertical carousel should not have RTL-specific behavior
            slick.slickSetOption('rtl', slick.options.vertical ? false : Resources.IS_RTL);
        });

        $carousel.slick(carouselOptions);
        if (carouselOptions.asNavForCarousel) {
            $carousel.addClass('b-carousel--as-nav').find('.slick-slide').filter('[data-slick-index=' + (carouselOptions.asSelectedSlide || 0) + ']').addClass('slick-selected').click();
        }
    }
}

/**
 * Find and initialize all carousels on selector
 * @param {Object} selector
 */
function initAllCarousels(selector) {
    $(selector).find(carouselInitOptions.baseSelector).each(function () {
        initSlick(this);
    });
}

/**
 * Initialize carousel
 * @param {Object} selector
 * @param {Object} params
 * @param callback
 */
function initCarousel(selector, params, callback) {
    if (!selector || $(selector).hasClass('slick-initialized')) {
        return;
    }

    if (!initialized) {
        initCarouselOptions(params);
        initialized = true;
    }

    if (callback) {
        $(selector).on('init', callback);
    }

    initSlick(selector, params);
}

/**
 * Reinitialize carousel
 * @param {Object} selector
 * @param {Object} params
 * @param callback
 */
function reInitCarousel(selector, params, callback) {
    if (!selector) {
        return;
    }

    if (callback) {
        $(selector).on('init', callback);
    }

    $(selector).slick('unslick');
    initSlick(selector, params);
}

/**
 * Carousel component
 */
module.exports = {
    /**
     * Initialize component
     * @param {Object} params - slick options
     */
    init: function (params) {
        if (!initialized) {
            initCarouselOptions(params);
            initialized = true;
        }

        initAllCarousels(params.selector || document);
    },

    /**
     * Initialize carousel on element
     * @param {Object} selector
     * @param {Object} params
     * @param callback
     */
    initCarousel: initCarousel,

    /**
     * Initialize all carousels on selector
     * @param {Object} selector
     * @param callback
     */
    initCarousels: function (selector, params, callback) {
        $(selector).find(carouselInitOptions.baseSelector).each(function () {
            if (!$(this).hasClass('slick-initialized')) {
                initCarousel(this, params, callback);
            }
        });
    },

    /**
     * Reinitialize carousel on selector
     * @param {Object} selector
     * @param {Object} params
     * @param callback
     */
    reInitCarousel: function(selector, params, callback) {
        $(selector).each(function () {
            if ($(this).hasClass('slick-initialized')) {
                reInitCarousel(this, params, callback);
            }
        });
    },

    /**
     * Destroy carousel by selector
     * @param {Object} selector
     */
    destroyCarousel: function(selector) {
        var $selector = $(selector);

        if ($selector.hasClass('slick-initialized')) {
            $selector.slick('unslick');
        }
    },

    /**
     * Pauses autoplay carousel by selector
     * @param {Object} selector
     */
    pauseCarousel: function(selector) {
        var $selector = $(selector);

        if ($selector.hasClass('slick-initialized')) {
            $selector.slick('slickPause');
        }
    },

    /**
     * Play autoplay carousel by selector
     * @param {Object} selector
     */
    playCarousel: function(selector) {
        var $selector = $(selector);

        if ($selector.hasClass('slick-initialized')) {
            $selector.slick('slickPlay');
        }
    }
};
