var latestKnownScrollY = 0,
    handlers = [],
    handler,
    blocked = false,
    elementNum = 0;

/**
 * @function
 * @description Execute scroll handlers
 * @returns {Void}
 */
function executeHandlers() {
    for (var i = 0; i < handlers.length; i++) {
        handler = handlers[i];
        handler.func(latestKnownScrollY, handler.$element, handler.params);
    }
    blocked = false;
}

/**
 * @function
 * @description Request nearest frame
 * @returns {Void}
 */
function requestNearestFrame() {
    if (blocked === false) {
        window.requestAnimationFrame(executeHandlers);
    }
    blocked = true;
}

/**
 * @function
 * @description Update scrollY
 * @returns {Void}
 */
function onScroll() {
    if (!window.disableScroll) {
        latestKnownScrollY = $(window).scrollTop();
        requestNearestFrame();
    }
}

/**
 * @function
 * @description Find handler in registered handlers array
 * @returns {Number} - foundedIndex
 */
function getHandlerIndex(handler, $element) {
    var foundedIndex = -1;

    for (var i = 0; i < handlers.length; i++) {
        var registeredHandler = handlers[i];

        if (registeredHandler.func.name === handler.name) {
            if ($element && registeredHandler.$element) {
                if (registeredHandler.$element.data('elementNum') === $element.data('elementNum')) {
                    foundedIndex = i;
                }
            } else {
                foundedIndex = i;
            }
        }
    }

    return foundedIndex;
}

$(window).on('scroll', onScroll);
$(document).on('touchstart', onScroll);

onScroll();

module.exports = {
    registerHandler: function (handler, $element, params) {
        if ($element) {
            $element.data('elementNum', elementNum);
            elementNum += 1;
        }

        if (getHandlerIndex(handler, $element) === -1) {
            handlers.push({func: handler, $element: $element, params: params});
        }
    },
    unRegisterHandler : function(handler, $element) {
        if (getHandlerIndex(handler, $element) !== -1) {
            handlers.splice(getHandlerIndex(handler, $element), 1);
        }
    }
};
