/**
 * Used for strict device detection, for some specific scripts, when we need to know which device used.
 * For other cases, related to device view, please, use layout.js
 */
var deviceUtils = {

    isDesktop: function() {
        return !this.isMobile() && !this.isTablet();
    },

    isMobile: function () {
        var mobileAgentHash = ['mobile', 'phone', 'ipod', 'android', 'blackberry', 'windows ce', 'opera mini', 'palm'];
        var idx = 0;
        var isMobile = false;
        var userAgent = (navigator.userAgent).toLowerCase();

        if (!this.isTablet()) {
            while (mobileAgentHash[idx] && !isMobile) {
                isMobile = (userAgent.indexOf(mobileAgentHash[idx]) >= 0);
                idx++;
            }
        }
        return isMobile;
    },

    isTablet: function() {
        var mobileAgentHash = ['tablet', 'ipad'];
        var idx = 0;
        var isTablet = false;
        var userAgent = (navigator.userAgent).toLowerCase();

        while (mobileAgentHash[idx] && !isTablet) {
            isTablet = (userAgent.indexOf(mobileAgentHash[idx]) >= 0);
            idx++;
        }
        return isTablet;
    },

    isTouch: function() {
        var mobileAgentHash = ['tablet', 'ipad', 'mobile', 'phone', 'ipod', 'android', 'blackberry', 'windows ce', 'opera mini', 'palm'];
        var idx = 0;
        var isTouch = false;
        var userAgent = (navigator.userAgent).toLowerCase();

        while (mobileAgentHash[idx] && !isTouch) {
            isTouch = (userAgent.indexOf(mobileAgentHash[idx]) >= 0);
            idx++;
        }
        return isTouch;
    },

    isIPhone: function() {
        var mobileAgentHash = ['iphone', 'ipod'];
        var idx = 0;
        var isIPhone = false;
        var userAgent = (navigator.userAgent).toLowerCase();

        if (!this.isTablet()) {
            while (mobileAgentHash[idx] && !isIPhone) {
                isIPhone = (userAgent.indexOf(mobileAgentHash[idx]) >= 0);
                idx++;
            }
        }
        return isIPhone;
    },

    isPortrait: function() {
        return window.innerHeight >= window.innerWidth;
    },

    isLandscape: function() {
        return window.innerHeight < window.innerWidth;
    }
};

module.exports = deviceUtils;
