'use strict';

var layout = require('./../utils/layout'),
    scrollListener = require('./../utils/scroll-listener'),
    util = require('./../util'),
    cssConstants = require('./../utils/cssconstants'),
    stickyElementsDefaultOptions = require('./../components-config/sticky-elements-config-default'),
    options = {},
    cssSelectors = {
        'stickyHeader' : '.js-sticky-header',
        'stickySearchOptions': '.js-plp__sticky-options',
        'stickyProductActions': '.js-pdp__sticky-actions',
        'stickyCartSummary': '.js-sticky-cart-summary',
        'select': '.js-select'
    },
    $cache = {},
    initialized = false;

/**
 * @function
 * @description Merge sticky elements options with params
 * @param {Object} params
 * @returns {void}
 */
function initStickyElementsOptions(params) {
    options = $.extend(true, options, stickyElementsDefaultOptions, params || {});

    if ('initOnBreakpoint' in params) {
        options.initOnBreakpoint = params.initOnBreakpoint;
    }
}

/**
 * @function
 * @description Initialize cache
 * @param {String} selector
 * @returns {void}
 */
function initCache(selector) {
    $cache.context = $(selector);
    $cache.window = $(window);
    $cache.document = $(document);
    $cache.html = $('html');
    $cache.page = $('.js-page');
    $cache.stickyHeader = $cache.context.find(cssSelectors.stickyHeader);
    $cache.stickySearchOptions = $cache.context.find(cssSelectors.stickySearchOptions);
    $cache.stickyProductActions = $cache.context.find(cssSelectors.stickyProductActions);
    $cache.stickyCartSummary = $cache.context.find(cssSelectors.stickyCartSummary);
}

/**
 * @function
 * @description Initialize events for sticky elements
 * @returns {Void}
 */
function initEvents() {
    $cache.document
    .on('window.modechanged window.orientationchange', function () {
        initAllStickyElements();
    })
    .on('product.search.update', function (e, data) {
        $cache.stickySearchOptions = $(data.context).find(cssSelectors.stickySearchOptions);
        initStickySearchOptions();
    })
    .on('pdp.content.update', function (e, data) {
        scrollListener.unRegisterHandler(stickyElementsHandler, $cache.stickyProductActions);
        $cache.stickyProductActions = $(data.context).find(cssSelectors.stickyProductActions);
        initStickyProductActions();
    })
    .on('cart.update', function (e, data) {
        scrollListener.unRegisterHandler(stickyElementsHandler, $cache.stickyCartSummary);
        $cache.stickyCartSummary = $(data.context).find(cssSelectors.stickyCartSummary);
        initStickyCartSummary();
    });
}

/**
 * @function
 * @description Update Sticky Element
 * @param {Number} scrollY
 * @param {Object} $element
 * @param {Object} stickyElementOptions
 */
function updateStickyElement(scrollY, $element, stickyElementOptions) {
    if (scrollY > stickyElementOptions.startPoint) {
        if (!stickyElementOptions.isStuck) {
            enableStickyState($element, stickyElementOptions, true);
        }

        if (stickyElementOptions.stuckInParent) {
            checkStuckInParentState(scrollY, $element, stickyElementOptions);
        }
    }

    if ((scrollY <= stickyElementOptions.startPoint) || (stickyElementOptions.stopPoint && scrollY > stickyElementOptions.stopPoint)) {
        if (stickyElementOptions.isStuck) {
            disableStickyState($element, stickyElementOptions);
        }
    }
}

/**
 * @function
 * @description Enable "Stuck in parent" state
 * @param {Number} scrollY
 * @param {Object} $element
 * @param {Object} stickyElementOptions
 */
function checkStuckInParentState(scrollY, $element, stickyElementOptions) {
    if (scrollY >= stickyElementOptions.stuckInParentTop) {
        $element.addClass(cssConstants.STUCK_IN_PARENT);
    } else {
        $element.removeClass(cssConstants.STUCK_IN_PARENT);
    }
}

/**
 * @function
 * @description Enable Sticky State
 * @param {Object} $element
 * @param {Object} stickyElementOptions
 * @param {Object} isScrolled - Get window.scrolY and activate sticky state if element riched active position
 */
function enableStickyState($element, stickyElementOptions, isScrolled) {
    if (isScrolled || window.scrollY > stickyElementOptions.startPoint) {
        updateActiveClasses($element, true, stickyElementOptions.activeHTMLclass);

        updateSpacer($element, stickyElementOptions, stickyElementOptions.elementInitialHeight);

        if (stickyElementOptions.stuckSide === 'bottom') {
            var elementHeight = $element.outerHeight();

            $cache.page.css('padding-bottom', elementHeight);
        }

        if (stickyElementOptions.selfWidth) {
            $element.outerWidth($element.parent().width());
        }

        unFocusElements($element);

        stickyElementOptions.isStuck = true;

        if (stickyElementOptions.setDataInWindow) {
            window[stickyElementOptions.setDataInWindow] = {
                'enabled': true,
                'height': stickyElementOptions.elementInitialHeight
            };
        }
    }
}

/**
 * @function
 * @description Disable Sticky State
 * @param {Object} $element
 * @param {Object} stickyElementOptions
 */
function disableStickyState($element, stickyElementOptions) {
    updateActiveClasses($element, false, stickyElementOptions.activeHTMLclass);

    updateSpacer($element, stickyElementOptions, 0);

    if (stickyElementOptions.stuckSide === 'bottom') {
        $cache.page.css('padding-bottom', 0);
    }

    if (stickyElementOptions.selfWidth) {
        $element.css('width', '');
    }

    if (stickyElementOptions.stuckInParent) {
        $element.removeClass(cssConstants.STUCK_IN_PARENT);
    }

    unFocusElements($element);

    stickyElementOptions.isStuck = false;

    if (stickyElementOptions.setDataInWindow) {
        window[stickyElementOptions.setDataInWindow] = {
            'enabled': false
        };
    }
}

/**
 * @function
 * @description This handler will be called to recalculate sticky header block on 'scroll' event
 * @param {Number} scrollY - scroll top position
 */
function stickyElementsHandler(scrollY, $element, stickyElementOptions) {
    updateStickyElement(scrollY, $element, stickyElementOptions);
}

/**
 * @function
 * @description Add or remove sticky class for element or HTML tag
 * @param {Object} $element
 * @param {Boolean} activate
 * @param {String} activeHTMLclass
 * @returns {Void}
 */
function updateActiveClasses($element, activate, activeHTMLclass) {
    $element.toggleClass(cssConstants.STICKY, activate);

    if (activeHTMLclass) {
        $cache.html.toggleClass(activeHTMLclass, activate);
    }
}

/**
 * @function
 * @description Initialize sticky elements
 * @param {Object} $element - jQuery Object
 * @returns {Void}
 */
function initStickyElement($element, params) {
    if ($element.length === 0) {
        return false;
    }

    var stickyElementDataOptions = $element.data('sticky') || {};
    var stickyElementOptions = $.extend({}, options, stickyElementDataOptions, params || {});

    if ($element.data('sticky-inited') === true) {
        unInitStickyElement($element, stickyElementOptions);
    }

    if ('initOnBreakpoint' in stickyElementDataOptions) {
        stickyElementOptions.initOnBreakpoint = stickyElementDataOptions.initOnBreakpoint;
    }

    var initOnBreakpoint = stickyElementOptions.initOnBreakpoint.length === 0 || stickyElementOptions.initOnBreakpoint.indexOf(layout.getMode()) !== -1;

    if (!initOnBreakpoint) {
        return;
    }

    stickyElementOptions.elementInitialHeight = $element.outerHeight();

    if (stickyElementOptions.startPoint === 'self') {
        stickyElementOptions.startPoint = $element.offset().top;

        if (window.disableScroll) {
            stickyElementOptions.startPoint += $cache.page.scrollTop();
        }

        if (stickyElementOptions.startPointSelfShift) {
            stickyElementOptions.startPoint = stickyElementOptions.startPoint - stickyElementOptions.startPointSelfShift;
        }

        if (stickyElementOptions.fullyHidden) {
            stickyElementOptions.startPoint = stickyElementOptions.startPoint + stickyElementOptions.elementInitialHeight;
        }
    }

    if (stickyElementOptions.selfWidth) {
        $cache.document.on('window.resize.setStickyElementWidth', function() {
            if (stickyElementOptions.isStuck) {
                $element.outerWidth($element.parent().width());
            }
        });
    }

    if (stickyElementOptions.stuckInParent) {
        var $stickyParent = $element.closest(stickyElementOptions.parent);

        stickyElementOptions.stuckInParentTop = 0;

        if ($stickyParent && $stickyParent.length !== 0) {
            stickyElementOptions.stuckInParentTop = $stickyParent.offset().top + $stickyParent.outerHeight() - $element.outerHeight();

            checkStuckInParentState($cache.window.scrollTop(), $element, stickyElementOptions);

            util.watchProperty($element, 'clientHeight', function(e, oldHeight, newHeight) {
                stickyElementOptions.stuckInParentTop = $stickyParent.offset().top + $stickyParent.outerHeight() - newHeight;
            }, 500);
        }
    }

    if (initOnBreakpoint) {
        enableStickyState($element, stickyElementOptions);
        scrollListener.registerHandler(stickyElementsHandler, $element, stickyElementOptions);
        $element.data('sticky-inited', true);
    }
}

/**
 * @function
 * @description Destroy sticky elements
 * @param {Object} $element - jQuery Object
 * @param {Object} stickyElementOptions - options
 * @returns {Void}
 */
function unInitStickyElement($element, stickyElementOptions) {
    disableStickyState($element, stickyElementOptions);

    scrollListener.unRegisterHandler(stickyElementsHandler, $element);

    if (stickyElementOptions.selfWidth) {
        $cache.document.off('window.resize.setStickyElementWidth');
    }

    if (stickyElementOptions.stuckInParent) {
        $element.removeClass(cssConstants.STUCK_IN_PARENT);

        util.stopWatchProperty($element, 'clientHeight');
    }

    $element.data('sticky-inited', false);
}

/**
 * @function
 * @description Keep static height of element
 * @param {Number} height - height of element
 * @returns {Void}
 */
function updateSpacer($element, stickyElementOptions, height) {
    var $spacerElement = $element.next(stickyElementOptions.spacer);

    if ($spacerElement.length === 0) {
        return;
    }

    $spacerElement.height(height);
}

/**
 * @function
 * @description Unfocus focusable elements 
 * @returns {Void}
 */
function unFocusElements($element) {
    $element.find('select:focus').blur();

    $element.find(cssSelectors.select).each(function() {
        var $this = $(this);
        if ($this.data('select2') && $this.data('select2').isOpen()) {
            $this.select2('close');
        }
    });
}

/**
 * @function
 * @description Initialize sticky header
 * @returns {Void}
 */
function initStickyHeader() {
    if (options.disableStickyHeader || !$cache.stickyHeader) {
        return;
    }
    initStickyElement($cache.stickyHeader);
}

/**
 * @function
 * @description Initialize sticky options on PLP
 * @returns {Void}
 */
function initStickySearchOptions() {
    if (!options.enableStickySearchOptions || !$cache.stickySearchOptions) return;

    initStickyElement($cache.stickySearchOptions);
}

/**
 * @function
 * @description Initialize sticky add to cart (PDP)
 * @returns {Void}
 */
function initStickyProductActions() {
    if (!options.enableStickyProductActions || !$cache.stickyProductActions) return;

    var stickyHeaderHeight = 0;

    if ($cache.stickyHeader && $cache.stickyHeader.data('sticky-inited') === true)  {
        stickyHeaderHeight = $cache.stickyHeader.outerHeight();
    }

    var params = {
        'startPointSelfShift' : stickyHeaderHeight
    }

    initStickyElement($cache.stickyProductActions, params);
}

/**
 * @function
 * @description Initialize sticky cart summary
 * @returns {Void}
 */
function initStickyCartSummary() {
    initStickyElement($cache.stickyCartSummary);
}

/**
 * @function
 * @description Initialize all sticky elements
 * @returns {Void}
 */
function initAllStickyElements() {
    initStickyHeader();
    initStickySearchOptions();
    initStickyProductActions();
    initStickyCartSummary();
}

var stickyelements = {
    init : function (params) {
        if (initialized) return;

        initStickyElementsOptions(params);
        initCache(params.selector || document);

        initAllStickyElements();

        initEvents();
        initialized = true;
    },
    initStickyCartSummary: function () {
        initStickyElement($cache.context.find(cssSelectors.stickyCartSummary));
    },
    getStickyHeaderHeight: function() {
        var height = 0;

        if ('stickyHeaderData' in window && 'enabled' in window.stickyHeaderData && window.stickyHeaderData.enabled) {
            height = window.stickyHeaderData.height;
        }

        return height;
    }
}

module.exports = stickyelements;
