'use strict';

var initialized = false,
    cssConstants = require('./../utils/cssconstants'),
    util = require('./../util'),
    layout = require('./../utils/layout'),
    tooltip = require('./tooltip'),
    $cache = {};

function updateCharsNumLeft($element) {
    var $charsNumElement = $element.closest('.f-field').find('.js-chars-num-left');
    $charsNumElement.text($element.prop('maxlength') - $element.val().length);
}

function initializeTextArea() {
    $('.js-textarea').each(function() {
        var $element = $(this);
        if ($element.data('initialized')) {
            return;
        }
        $element.data('initialized', true);
        $element.on('keyup', util.debounce(function() {
            updateCharsNumLeft($(this));
        }, 200));
        updateCharsNumLeft($element);
    });
}

function initializeCookieMessage() {
    if (document.cookie.length === 0) {
        $('.js-cookie-check').removeClass(cssConstants.HIDDEN);
    }
}

function initializePhoneNumberLinks(container) {
    if (layout.isExtraSmall()) {
        container = container || document;
        $(container).find('.js-phone-number-link').each(function() {
            var $newElement = $('<a>', {
                'html': $(this).html(),
                'href': 'tel:' + $(this).data('phone-number'),
            });

            $.each(this.attributes, function() {
                $newElement.attr(this.name, this.value);
            });

            $(this).replaceWith($newElement);
        });
    }
}

function initializeCache() {
    $cache.document = $(document);
}

function initializeEvents() {
    // generic toggle functionality
    $cache.document.on('click', '.js-toggle', function (e) {
        e.preventDefault();

        var $toggleButton = $(this),
            $toggleContent = $toggleButton.data('toggleContent'),
            $toggleScope = $toggleButton.data('toggleScope');

        if (!$toggleContent) {
            $toggleContent = $toggleButton.next('.js-toggle-content');
            $toggleButton.data('toggleContent', $toggleContent);
        } else if (!($toggleContent instanceof jQuery)) {
            $toggleContent = $toggleScope ? $toggleButton.closest($toggleScope).find($toggleContent) : $($toggleContent);
            $toggleButton.data('toggleContent', $toggleContent);
        }

        $toggleButton.toggleClass(cssConstants.EXPANDED)
        $toggleContent.toggleClass(cssConstants.EXPANDED);
    });

    // read more expand
    $('.js-read-more__btn').each(function() {
        var $readMoreBtn = $(this),
            $readMoreText = $readMoreBtn.closest('.js-read-more').find('.js-read-more__text');

        $readMoreBtn.on('click', function(e) {
            e.preventDefault();
            $readMoreBtn.toggleClass(cssConstants.EXPANDED);
            $readMoreText.slideToggle(200, 'linear');
        });
    });

    // Wishlist social sharing tooltip
    $('.js-wishlist-share').on('click', function() {
        tooltip.open($(this), {
            content: $('.js-wishlist-share-content').html(),
            showCloseButton: false
        });

        $cache.document.trigger('wishlist.socialshare');
    });
}

module.exports = {
    init: function () {
        if (initialized) return;
        initializeCache();
        initializeEvents();
        initializeTextArea();
        initializeCookieMessage();
        initializePhoneNumberLinks();
        initialized = true;
    },
    initPhoneNumberLinks: initializePhoneNumberLinks,
    initTextArea: initializeTextArea
};
