'use strict'

var layout = require('./../utils/layout'),
    stickyElements = require('./sticky-elements'),
    util = require('./../util');
var initialized = false,
    accordionDefaultOptions = require('./../components-config/accordion-config-default'),
    accordionInitOptions = {};

var $cache = {};

/**
 * @function
 * @description Initialize cache
 * @returns {void}
 */

function initCache() {
    $cache.document = $(document);
}

/**
 * @function
 * @description Initialize default accordion options
 * @param {Object} params
 * @returns {void}
 */
function initAccordionOptions(params) {
    accordionInitOptions = $.extend({}, accordionDefaultOptions, params || {});
}

/**
 * @function
 * @description Initialize custom accordion plugin based on jQuery UI Accordion
 * @returns {void}
 */
function initAccordion(context) {
    $(context).find(accordionInitOptions.baseSelector).each(function() {
        var $accordion = $(this),
            accordionDataOptions = $accordion.data('accordion'),
            accordionOptions = $.extend({}, accordionInitOptions, accordionDataOptions || {});

        if ('isCollapsibleForMode' in accordionOptions && 'object' === typeof accordionOptions.isCollapsibleForMode) {
            var currentLayoutMode = layout.getMode();
            if (currentLayoutMode in accordionOptions.isCollapsibleForMode) {
                accordionOptions.collapsible = accordionOptions.isCollapsibleForMode[currentLayoutMode];
            }

            $cache.document.on('window.resize', function() {
                currentLayoutMode = layout.getMode();
                var isCollapsible = currentLayoutMode in accordionOptions.isCollapsibleForMode ? accordionOptions.isCollapsibleForMode[currentLayoutMode] : false;
                $accordion.accordion('option', 'collapsible', isCollapsible);
            });
        }

        $accordion.accordion(accordionOptions);
    });
}

/**
 * @function
 * @description Initialize events
 * @returns {void}
 */
function initEvents() {
    $cache.document.on('click', accordionInitOptions.linkSelector, function(e) {
        e.preventDefault();
        var $accordionHeader = $($(this).attr('href'));

        if ($accordionHeader.length === 0) {
            return false;
        }

        var $accordion = $accordionHeader.closest(accordionInitOptions.baseSelector),
            headerIndex = $accordionHeader.index('.ui-accordion-header');

        $accordion.accordion('option', 'active', headerIndex);

        var stickyHeaderHeight = stickyElements.getStickyHeaderHeight();
        util.scrollBrowser($accordionHeader.offset().top, 0, stickyHeaderHeight);
    });

    // solve mobile (mostly iOS) issue of accordion tabs getting out of view on click
    $cache.document.on('click', accordionInitOptions.baseSelector, function() {
        if (util.isMobile()) {
            var $this = $(this);
            var stickyHeaderHeight = stickyElements.getStickyHeaderHeight();

            if (!util.elementInViewport($this[0], stickyHeaderHeight)) {
                util.scrollBrowser($this.offset().top, 0, stickyHeaderHeight);
            }
        }
    });
}

module.exports = {
    init: function (params) {
        if (initialized) return;
        initCache();
        initAccordionOptions(params);
        initAccordion(params.container || document);
        initEvents();
        initialized = true;
    }
};
