'use strict';

/* Include Select2 plugin */
require('select2')();

var device = require('./../utils/device');
var $cache = {},
    defaultOptions = require('./../components-config/select-config-default'),
    selectInitOptions = {};

/* Usage:
*  Configuration API: https://select2.org/configuration/options-api
*  HTML Example:
*  <select data-select='{"minimumResultsForSearch": 0}' class="f-select js-select">
*      <option>Select option 1</option>
*      <option>Select option 2</option>
*  </select>
*/

function initConfig(params) {
    selectInitOptions = $.extend(true, defaultOptions, params || {});
}

function initCache(selector) {
    $cache.select = $(selector).find(selectInitOptions.baseSelector);
}

function initEvents() {
    // validate selectbox after change if it is part of the form
    $('form').on('change', selectInitOptions.baseSelector, function(e) {
        $(e.target).valid();
    });
}

function initTemplate(state, container, options, setNativeClasses) {
    if (!state.id) return state.text;
    var optionText = '<span class="select2-results__option-text ' + options.optionCssClass +  '">' + state.text.trim() + '</span>';

    if ('image' in state.element.dataset) {
        optionText = '<span class="select2-results__option-image"><img class="select2-results__image" src="' + state.element.dataset.image + '"/></span>' + optionText;
    }

    if (setNativeClasses && state.element) {
        $(container).addClass($(state.element).attr('class'));
    }

    return $(optionText);
}

function initSelect() {
    $cache.select.each(function() {
        var $this = $(this);
        var dataOptions = $this.data('select');
        var options = $.extend({}, defaultOptions, dataOptions || {});

        options.templateResult = function(state, container) {
            return initTemplate(state, container, options, true);
        };

        options.templateSelection = function(state, container) {
            return initTemplate(state, container, options);
        };

        if (!options.placeholder) {
            options.placeholder = $this.find('option[value=""]').text();
        }

        $this.select2(options);

        // disable 'select2' focus event on touch devices to properly show native dropdown
        if (device.isTouch()) $this.off('focus.select2');
    });
}

var Select = {
    init: function(params) {
        initConfig(params);
        initCache(params.container || document);
        initEvents();
        initSelect();
    }
};

/**
 * Export Select component
 */
module.exports = Select;
