var util = require('./../util'),
    deviceUtils = require('./device'),
    cssConstants = require('./cssconstants'),
    initialized = false,
    $cache = {},
    viewportWidth = {
        'extra-small': { 
            'minWidth': 0,
            'maxWidth': 767
        },
        'small': {
            'minWidth': 768,
            'maxWidth': 1024
        },
        'medium': { 
            'minWidth': 1025,
            'maxWidth': 1439
        },
        'large': { 
            'minWidth': 1440,
            'maxWidth': Infinity
        }
    };

function initializeCache() {
    $cache.document = $(document);
    $cache.window = $(window);
    $cache.html = $('html');
}

function initializeEvents() {
    var viewMode = getLayoutMode(),
        viewOrientation = getWindowOrientation();

    $cache.html.addClass(deviceUtils.isTouch() ? 'touch' : 'non-touch');

    // Add specific class to identify when keyboard is opened or closed on touch devices
    if (deviceUtils.isTouch()) {
        $cache.html.addClass(cssConstants.KEYBOARD_IS_CLOSED);
        $cache.document.on('focus', '[type=text], [type=email], [type=password], textarea', function(e) {
            $cache.html.removeClass(cssConstants.KEYBOARD_IS_CLOSED).addClass(cssConstants.KEYBOARD_IS_OPENED);
            $(e.target).one('blur', function() {
                $cache.html.removeClass(cssConstants.KEYBOARD_IS_OPENED).addClass(cssConstants.KEYBOARD_IS_CLOSED);
            });
        });
    }

    $cache.window.on('resize', util.eventDelay(function() {
        $cache.document.trigger('window.resize');

        var windowOrientation = getWindowOrientation();

        if (viewOrientation !== windowOrientation) {
            viewOrientation = windowOrientation;
            $cache.document.trigger('window.orientationchange', {'orientation': viewOrientation});
        }

        var layoutView = getLayoutMode();
        if (viewMode !== layoutView) {
            viewMode = layoutView;
            $cache.document.trigger('window.modechanged', {mode: viewMode});
        }
    }, 500))
    .on('scroll', util.eventDelay(function() {
        $cache.document.trigger('window.scroll', {scrollTop: $(this).scrollTop()});
    }, 300));

}

function getLayoutMode() {
    // to determine layout width should be used window property "innerWidth", 
    // because it contains window width including scrollbars, which meets the value of width in mediaqueries in CSS
    var windowWidth = window.innerWidth;
    for (var mode in viewportWidth) {
        if (windowWidth >= viewportWidth[mode].minWidth && windowWidth  <= viewportWidth[mode].maxWidth) {
            return mode;
        }
    }
}

function getWindowOrientation() {
    return window.innerHeight > window.innerWidth ? 'portrait' : 'landscape';
}

module.exports = {
    viewports: viewportWidth,

    getMode: getLayoutMode,

    getWindowOrientation: getWindowOrientation,

    isExtraSmall: function() {
        return this.getMode() === 'extra-small';
    },

    isSmall: function() {
        return this.getMode() === 'small';
    },

    isMedium: function() {
        return this.getMode() === 'medium';
    },

    isLarge: function() {
        return this.getMode() === 'large';
    },

    isLandscape: function() {
        return window.innerHeight < window.innerWidth;
    },

    isPortrait: function() {
        return window.innerHeight > window.innerWidth;
    },

    init : function() {
        if (initialized) {return;}
        initializeCache();
        initializeEvents();
        initialized = true;
    }
};
