'use strict';

var Resources = window.Resources = {
    IS_RTL: false // add here all necessary values if other js-plugin will need it
}

var app = window.app = {
    init: function () {
        var components = references;

        for (var name in components) {
            try {
                components[name].init({});
                console.log(name, ' initialized');
            } catch (e) {
                console.log(e, '"' + name + '" initialization failed');
            }
        }
    }
};

/** The references object of all modules needed for components initialization */
var references = {
    /** Components from extension cartridge */
    'select' : require('./../../../../ecom-core/app_storefront_core_ext/cartridge/js/components/select'),
    'carousel' : require('./../../../../ecom-core/app_storefront_core_ext/cartridge/js/components/carousel'),
    'accordion' : require('./../../../../ecom-core/app_storefront_core_ext/cartridge/js/components/accordion'),
    'common' : require('./../../../../ecom-core/app_storefront_core_ext/cartridge/js/components/common')
};

// initialize app
$(document).ready(function () {
    $(window).on('styleguide:onRendered', function() {
        app.init();
    });
});
